import { CssBaseline } from "@mui/material";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Route, Routes, Navigate, useLocation } from "react-router-dom";
import Loader from "./components/Loader";
import Home from "./pages/Home";
import Login from "./pages/Login";
import Signup from "./pages/Signup";
import { checkAuth, selectAuth, selectLoading } from "./redux/slices/authSlice";



function App() {
  const location = useLocation();
  const dispatch = useDispatch()
  const isAuth = useSelector(selectAuth)
  const isLoading = useSelector(selectLoading)

  useEffect(() => {
    if (localStorage.getItem('token')) {
      dispatch(checkAuth())
    }
  }, [dispatch])

  if (isLoading) return (
    <>
      <CssBaseline>
        <Loader />
      </CssBaseline>
    </>
  )

  return (
    <>
      <CssBaseline />
      <Routes>
        <Route path="/" element={<Signup />} />
        {/* <Route path="Authorization-client/login" element={<Login />} />
        <Route path="Authorization-client/signup" element={<Signup />} />
        <Route
          path="Authorization-client/"
          element={
            isAuth ? (
              <Home />
            ) : (
              <Navigate to="Authorization-client/login" state={{ from: location }} replace />
            )
          }
        /> */}
      </Routes>
    </>
  );
}


export default App;
